import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name:'fth',
      redirect: '/fth'
    },
    {
      path: '/fth',
      component: resolve => require(['../views/HomePage'], resolve),
    },
    {
      path: '/errorinfo',
      name: 'Errorinfo',
      component: resolve => require(['../views/NotFound'], resolve),
    }

  ]
})




