import Vue from 'vue'
import VueI18n from 'vue-i18n'
import zhCNLocale from './zh-CN'
import enUSLocale from './en-US'

Vue.use(VueI18n)

const messages = {
  zhCN: {
    ...zhCNLocale,
  },
  enUS:{
    ...enUSLocale
  }
}
if(window.name != ''){
  sessionStorage.setItem('lang',window.name)
}
if(sessionStorage.getItem("lang") == 'enUS' || sessionStorage.getItem("lang") == '' || sessionStorage.getItem("lang") == null){
  sessionStorage.setItem('lang','enUS')
}
console.log(sessionStorage.getItem('lang'))
const i18n = new VueI18n({
  locale: sessionStorage.getItem("lang"), // set locale
  messages
})


export default i18n



